import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule } from '@angular/forms';
import { GitHubOverviewComponent } from './overview/git-hub-overview.component';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { FooterComponent } from './footer/footer.component';
import { SearchBarComponent } from './search-bar/search-bar.component';
import { RouterModule, Routes } from '@angular/router';
import { ConnectorsComponent } from './connectors/connectors.component';
import { HeaderComponent } from './header/header.component';
import { PreloaderComponent } from './preloader/preloader.component';
import { CustomPaginationControlsComponent } from './custom-pagination-controls/custom-pagination-controls.component';
import { NgxPaginationModule } from 'ngx-pagination';
import { IotComponent } from './iot/iot.component';
import { TextLimitPipe } from '../pipes/text-limit/text-limit.pipe';
import { MessageComponent } from './message/message.component';
import { ProjectCardComponent } from './project-card/project-card.component';

const appRoutes: Routes = [
  { path: '', component: GitHubOverviewComponent },
  { path: 'api-integration-microservices', component: ConnectorsComponent },
  { path: 'iot-analytics', component: IotComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    GitHubOverviewComponent,
    FooterComponent,
    SearchBarComponent,
    ConnectorsComponent,
    HeaderComponent,
    PreloaderComponent,
    CustomPaginationControlsComponent,
    IotComponent,
    MessageComponent,
    TextLimitPipe,
    ProjectCardComponent
  ],
  imports: [
    BrowserModule,
    NgbModule,
    HttpClientModule,
    FormsModule,
    FontAwesomeModule,
    RouterModule.forRoot(appRoutes),
    NgxPaginationModule,
  ],
  providers: [],
  bootstrap: [
    AppComponent
  ]
})
export class AppModule { }
