import { Component, OnInit } from '@angular/core';
import { SoftwareAgGithubTopics } from '../../interfaces/software-ag-github-topics';
import { faCodeBranch, faStar } from '@fortawesome/free-solid-svg-icons';
import { IotAnalyticsQueryParamsService } from '../../services/IoTAnalytics/IoTAnalyticsQueryParamsService/iot-analytics-query-params.service';
import { IotAnalyticsDataService } from '../../services/IoTAnalytics/IoTAnalyticsDataService/iot-analytics-data.service';
import { RepositoryType } from '../../interfaces/repository-type';
import { Constants } from '../../classes/constants';
import { WebPage } from '../message/message.component';
import { MessageService, MessageType } from '../../services/MessageService/message.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Meta, Title } from '@angular/platform-browser';
import { Subscription } from 'rxjs';
import { IoTSearchResult } from '../../interfaces/iot-search-result';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-iot',
  templateUrl: './iot.component.html',
  styleUrls: ['./iot.component.css']
})
export class IotComponent implements OnInit {

  private subscriptions = new Subscription();
  private initialPageLoad = true;

  public softwareAGTopics: SoftwareAgGithubTopics[] = [];
  public repositories: RepositoryType[] = [];
  public faStar = faStar;
  public faFork = faCodeBranch;

  public IoTAnalyticsWebpage = WebPage.IoTAnalytics;

  constructor(
    public iotAnalyticsDataService: IotAnalyticsDataService,
    private messageService: MessageService,
    private iotAnalyticsQueryParamsService: IotAnalyticsQueryParamsService,
    private titleService: Title,
    private metaTagService: Meta,
    private route: ActivatedRoute) {
  }

  ngOnInit(): void {
    this.iotAnalyticsQueryParamsService.initialize();
    this.subscriptions.add(this.iotAnalyticsDataService.lastSearchResult$.subscribe( (lastSearchResult: IoTSearchResult) => {
      // Only scroll on initial page load and where a dropdown or search text has been provided (in the url)
      const queryParamMap = this.route.snapshot.queryParamMap;
      if (this.initialPageLoad  && (queryParamMap.has('topic') || queryParamMap.has('repository') || queryParamMap.has('search') || queryParamMap.has('page'))) {
        setTimeout( () => {
          this.scrollToProjects();
        }, 0);
      }
      this.initialPageLoad = false;
    }));

    this.metaTagService.updateTag({ name: 'description', content: 'Discover and start using open source IoT and Analytics projects and code samples for products including Cumulocity IoT, TrendMiner, Apama Streaming Analytics, Zementis and more.'});
    this.metaTagService.updateTag( {name: 'keywords', content: 'internet of things, analytics, machine learning, streaming analytics, cumulocity iot, apama, zementis'});

    this.titleService.setTitle(`IoT & Analytics | ${Constants.CONST_APPLICATION_NAME}`);

    // Retrieve the list of Software AG Topics
    this.iotAnalyticsDataService.getTopics()
      .then( (softwareAGTopics: SoftwareAgGithubTopics[]) => {
        this.softwareAGTopics = softwareAGTopics;
        this.iotAnalyticsDataService.getRepositoryTypes()
          .then( (repoTypes: RepositoryType[]) => {
            this.repositories = repoTypes;
          });
      });
  }

  // User selected a Software AG Topic from the dropdown
  public selectedSoftwareAGTopic(topic): void {
    this.iotAnalyticsDataService.updateSearchParameters({
      topic,
      page: 1,
    });
    this.iotAnalyticsDataService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }

  // User selected a repo type from the dropdown
  public selectedRepository(repository): void {
    this.iotAnalyticsDataService.updateSearchParameters({
      repository,
      page: 1,
    });
    this.iotAnalyticsDataService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }

  // User entered text into the Search... field
  public searchGitHubProjectsByText(searchText: string): void {
    this.iotAnalyticsDataService.updateSearchParameters({
      text: searchText,
      page: 1,
    });
    this.iotAnalyticsDataService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }

  public goToPage(pageNumber): void {
    this.iotAnalyticsDataService.updateSearchParameters({
      page: pageNumber,
    });
    this.iotAnalyticsDataService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }

  public scrollToProjects(): void {
    document.querySelector('#all-iot-analytics-projects').scrollIntoView();
  }

  public selectedCumulocityUIWidgets(): void {
    this.iotAnalyticsDataService.updateSearchParameters({
      topic: 'cumulocity-iot',
      repository: 'widget',
      text: '',
      page: 1,
    });
    this.iotAnalyticsDataService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }

}
