import { Component, EventEmitter, Input, Output } from '@angular/core';
import { OverviewDataService } from '../../services/Overview/OverViewDataService/overview-data.service';

@Component({
  selector: 'app-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.css']
})
export class SearchBarComponent {

  @Input() searchText = '';
  @Input() gihHubService: OverviewDataService;
  @Output() search = new EventEmitter<string>();

  constructor() { }

  public searchForText(): void {
    this.search.emit(this.searchText);
  }

  public keyUp(event): void {
    if ( (event.key === 'Enter' && this.searchText !== '') || this.searchText === '') {
      this.searchForText();
    }
  }

}
