import { faCodeBranch, faStar } from '@fortawesome/free-solid-svg-icons';
import { SoftwareAgGithubTopics } from '../../interfaces/software-ag-github-topics';
import { Component, OnInit } from '@angular/core';
import { ConnectorsDataService } from '../../services/Connectors/ConnectorsDataService/connectors-data.service';
import { ConnectorsQueryParamsService } from '../../services/Connectors/ConnectorsQueryParamsService/connectors-query-params.service';
import { Constants } from '../../classes/constants';
import { WebPage } from '../message/message.component';
import { MessageService, MessageType } from '../../services/MessageService/message.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Meta, Title } from '@angular/platform-browser';
import { Subscription } from 'rxjs';
import { ConnectorsSearchResult } from '../../interfaces/connectors-search-result';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-connectors',
  templateUrl: './connectors.component.html',
  styleUrls: ['./connectors.component.css']
})
export class ConnectorsComponent implements OnInit {

  private subscriptions = new Subscription();
  private initialPageLoad = true;

  public softwareAGTopics: SoftwareAgGithubTopics[] = [];
  public faStar = faStar;
  public faFork = faCodeBranch;
  public apiIntegrationMicroserviceWebPage = WebPage.ApiIntegrationMicroservice;

  constructor(
    public connectorsDataService: ConnectorsDataService,
    private messageService: MessageService,
    private connectorsQueryParamsService: ConnectorsQueryParamsService,
    private titleService: Title,
    private metaTagService: Meta,
    private route: ActivatedRoute) {
  }

  ngOnInit(): void {
    this.connectorsQueryParamsService.initialize();
    this.subscriptions.add(this.connectorsDataService.lastSearchResult$.subscribe( (lastSearchResult: ConnectorsSearchResult) => {
      // Only scroll on initial page load and where a dropdown or search text has been provided (in the url)
      const queryParamMap = this.route.snapshot.queryParamMap;
      if (this.initialPageLoad  && (queryParamMap.has('topic') || queryParamMap.has('search') || queryParamMap.has('page'))) {
        setTimeout( () => {
          this.scrollToProjects();
        }, 0);
      }
      this.initialPageLoad = false;
    }));
    this.metaTagService.updateTag({ name: 'description', content: 'Discover and use open source APIs, Integration and Microservices projects and code samples for products including webMethods, webMethods.io Integration, API Management, B2B, Terracotta and more.'});
    this.metaTagService.updateTag({ name: 'keywords', content: 'webmethods, integration, webmethods.io, cloudstreams connectors, api management, b2b, managed file transfer, terracotta'});

    this.titleService.setTitle(`API, Integration & Microservices | ${Constants.CONST_APPLICATION_NAME}`);

    // Retrieve the list of Software AG Topics
    this.connectorsDataService.getTopics()
      .then( (softwareAGTopics: SoftwareAgGithubTopics[]) => {
        this.softwareAGTopics = softwareAGTopics;
      });
  }

  // User selected a Software AG Topic from the dropdown
  public selectedSoftwareAGTopic(topic): void {
    this.connectorsDataService.updateSearchParameters({
      topic,
      page: 1,
    });
    this.connectorsDataService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.apiIntegrationMicroserviceWebPage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.apiIntegrationMicroserviceWebPage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }

  // User entered text into the Search... field
  public searchGitHubProjectsByText(searchText: string): void {
    this.connectorsDataService.updateSearchParameters({
      text: searchText,
      page: 1,
    });
    this.connectorsDataService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.apiIntegrationMicroserviceWebPage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.apiIntegrationMicroserviceWebPage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }

  public scrollToProjects(): void {
    document.querySelector('#all-connectors-projects').scrollIntoView();
  }

  public goToPage(pageNumber): void {
    this.connectorsDataService.updateSearchParameters({
      page: pageNumber,
    });
    this.connectorsDataService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.apiIntegrationMicroserviceWebPage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.apiIntegrationMicroserviceWebPage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }
}
