import { Injectable } from '@angular/core';
import { WebPage } from 'src/app/message/message.component';
import { Message } from '../../Interfaces/message';

export enum MessageType {
  CONST_ERROR_TYPE = 'ERROR',
  CONST_WARNING_TYPE = 'WARNING',
  CONST_SUCCESS_TYPE = 'SUCCESS'
}

@Injectable({
  providedIn: 'root'
})
export class MessageService {
  // The time in seconds that a message displays on screen
  static readonly MESSAGE_TIMEOUT = 2500; // 2.5 seconds

  public webpage: WebPage;
  public message: Message;

  constructor() {
    this.message = {type: '', text: ''};
  }

  public setMessage(page: WebPage, type: MessageType, text: string, timeout?: number): void {
    this.webpage = page;
    this.message = {type, text};
    // set the default timeout if it hasn't been provided
    timeout = timeout || MessageService.MESSAGE_TIMEOUT;

    if (timeout > -1) {
      // clear the message
      setTimeout(() => {
        this.clearMessage();
      }, timeout);
    }
  }

  public clearMessage(): void {
    this.message.type = '';
    this.message.text = '';
  }
}
