import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { SoftwareAgGithubTopics } from '../../../interfaces/software-ag-github-topics';
import { BehaviorSubject, ReplaySubject } from 'rxjs';
import { GitHubCacheService } from '../../GitHubCacheService/git-hub-cache.service';
import { DataService } from '../../DataService/data.service';
import { RepositoryType } from '../../../interfaces/repository-type';
import { GitHubSearchParams } from '../../../interfaces/github-search-params';
import { GitHubSearchResult } from '../../../interfaces/github-search-result';
import { GetGitHubProjectsResponse } from '../../../interfaces/getgithubprojectsresponse';
import { GoogleAnalyticsService } from '../../GoogleAnalyticsService/google-analytics.service';

@Injectable({
  providedIn: 'root'
})
export class IotAnalyticsDataService  extends DataService implements DataService {

  private softwareAGGitHubTopics: SoftwareAgGithubTopics[] = this.topics.filter(topic => topic.page.includes(DataService.CONST_IOT_ANALYTICS_PAGE));

  private repositories: RepositoryType[] = [
    {id: 'all-repositories', name: 'All repositories', topics: []},
    {id: 'cumulocity-agent', name: 'cumulocity-agent', topics: ['cumulocity-agent']},
    {id: 'microservice', name: 'microservice', topics: ['microservice']},
    {id: 'widget', name: 'widget', topics: ['widget']}
  ];

  public currentSearchParameters$ = new BehaviorSubject<GitHubSearchParams>(
    {
      text: '',
      topic: 'all-topics',
      repository: 'all-repositories',
      page: 1,
      pageSize: 9
    }
  );

  public lastSearchResult$ = new ReplaySubject<GitHubSearchResult>(1);

  constructor(
    private http: HttpClient,
    private gitHubCacheService: GitHubCacheService,
    private gaService: GoogleAnalyticsService) {
    super();
  }

  // Return Software AG Topics
  public getTopics(): Promise<SoftwareAgGithubTopics[]> {
    return Promise.resolve(this.softwareAGGitHubTopics);
  }

  // Return Repository Types
  public getRepositoryTypes(): Promise<RepositoryType[]> {
    return Promise.resolve(this.repositories);
  }

  public resetFilters(): void {
    this.updateSearchParameters({
      topic: 'all-topics',
      repository: 'all-repositories',
      page: 1,
    });
    this.searchWithCurrentParams().then();
  }

  public updateSearchParameters(params: Partial<GitHubSearchParams>, ignorePageReset = false): void {
    const currentSearchParams = this.currentSearchParameters$.getValue();
    // If the filters exist and have been changed, reset the page to page 1
    if (!ignorePageReset &&
      (
        (params.text && params.text !== currentSearchParams.text) ||
        (params.topic && params.topic !== currentSearchParams.topic) ||
        (params.repository && params.repository !== currentSearchParams.repository)
      )
    ) {
      params.page = 1;
    }
    const newSearchParams = Object.assign({}, this.currentSearchParameters$.getValue(), params);
    this.currentSearchParameters$.next(newSearchParams);
  }

  public async searchWithCurrentParams(): Promise<GitHubSearchResult> {
    return this.search(this.currentSearchParameters$.getValue());
  }

  public async search(searchParams: GitHubSearchParams): Promise<GitHubSearchResult> {
    const softwareAGTopic = this.softwareAGGitHubTopics.find( t => t.id === searchParams.topic);
    const repository = this.repositories.find( r => r.id === searchParams.repository);

    let topics = '+topic:iot-analytics';
    if (softwareAGTopic && softwareAGTopic.topics.length > 0) {
      topics += softwareAGTopic.topics.filter(topic => topic.length > 0).reduce((result, topic) => result += `+topic:${topic}`, '');
    }
    if (repository && repository.topics.length > 0) {
      topics += repository.topics.filter(topic => topic.length > 0).reduce((result, topic) => result += `+topic:${topic}`, '');
    }

    let queryString = '';
    if (searchParams.text !== '') {
      queryString += `+${searchParams.text}+in:readme+${searchParams.text}+in:description+${searchParams.text}+in:name`;
    }

    if (topics !== '') {
      queryString += topics;
    }

    const response: GetGitHubProjectsResponse | HttpErrorResponse = await this.gitHubCacheService.search('iot', queryString, searchParams.page, searchParams.pageSize);
    const getGitHubProjectsResponse = (response as GetGitHubProjectsResponse);
    const searchResult = {
      params: searchParams,
      response: getGitHubProjectsResponse
    };
    this.lastSearchResult$.next(searchResult);
    this.gaService.pushToGoogleAnalytics();
    return searchResult;
  }
}
