import { Component, OnDestroy, OnInit } from '@angular/core';
import { OverviewDataService } from '../../services/Overview/OverViewDataService/overview-data.service';
import { Subscription } from 'rxjs';
import { SoftwareAgGithubTopics } from '../../interfaces/software-ag-github-topics';
import { OverviewQueryParamsService } from '../../services/Overview/OverviewQueryParamsService/overview-query-params.service';
import { WebPage } from '../message/message.component';
import { MessageService, MessageType } from '../../services/MessageService/message.service';
import { Constants } from '../../classes/constants';
import { HttpErrorResponse } from '@angular/common/http';
import { Meta, Title } from '@angular/platform-browser';
import { OverviewSearchResult } from '../../interfaces/overview-search-result';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-github-overview',
  templateUrl: './git-hub-overview.component.html',
  styleUrls: ['./git-hub-overview.component.css']
})
export class GitHubOverviewComponent implements OnInit, OnDestroy {

  private subscriptions = new Subscription();
  private initialPageLoad = true;

  public softwareAGTopics: SoftwareAgGithubTopics[] = [];
  public overViewWebpage = WebPage.Overview;

  constructor(
    public overviewService: OverviewDataService,
    public messageService: MessageService,
    private overviewQueryParamsService: OverviewQueryParamsService,
    private titleService: Title,
    private metaTagService: Meta,
    private route: ActivatedRoute) {
  }

  ngOnInit(): void {
    this.overviewQueryParamsService.initialize();
    this.subscriptions.add(this.overviewService.lastSearchResult$.subscribe( (lastSearchResult: OverviewSearchResult) => {
      // Only scroll on initial page load and where a dropdown or search text has been provided (in the url)
      const queryParamMap = this.route.snapshot.queryParamMap;
      if (this.initialPageLoad  && (queryParamMap.has('topic') || queryParamMap.has('search') || queryParamMap.has('page'))) {
        setTimeout( () => {
          this.scrollToProjects();
        }, 0);
      }
      this.initialPageLoad = false;
    }));

    this.metaTagService.updateTag({ name: 'description', content: 'Software AG Open Source – discover projects by Software AG and partners, code samples and resources to accelerate innovation.'});
    this.metaTagService.updateTag( {name: 'keywords', content: 'github, open source, code, webmethods, integration, cumulocity iot, internet of things, adabas, apama, zementis'});

    this.titleService.setTitle(`${Constants.CONST_APPLICATION_NAME}`);

    // Retrieve the list of Software AG Topics
    this.overviewService.getTopics()
      .then( (softwareAGTopics: SoftwareAgGithubTopics[]) => {
        this.softwareAGTopics = softwareAGTopics;
    });
  }

  // User selected a Software AG Topic from the dropdown
  public selectedSoftwareAGTopic(topic): void {
    this.overviewService.updateSearchParameters({
      topic,
      page: 1,
    });
    this.overviewService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.overViewWebpage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.overViewWebpage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }

  // User entered text into the Search... field
  public searchGitHubProjectsByText(searchText: string): void {
    this.overviewService.updateSearchParameters({
      text: searchText,
      page: 1,
    });
    this.overviewService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.overViewWebpage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.overViewWebpage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }

  public goToPage(pageNumber): void {
    this.overviewService.updateSearchParameters({
      page: pageNumber,
    });
    this.overviewService.searchWithCurrentParams()
      .catch( (error) => {
        const errorResponse = (error as HttpErrorResponse);
        if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
          this.messageService.setMessage(this.overViewWebpage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
        } else {
          this.messageService.setMessage(this.overViewWebpage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
        }
    });
  }

  public scrollToProjects(): void {
    document.querySelector('#all-projects').scrollIntoView();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

}
