import { Component, Input } from '@angular/core';
import { MessageService } from '../../services/MessageService/message.service';


export enum WebPage {
  Overview = 1,
  ApiIntegrationMicroservice,
  IoTAnalytics
}

@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css']
})
export class MessageComponent {
  static readonly WebPage = WebPage;

  @Input() webpage: WebPage;

  constructor(private messageService: MessageService) { }

  public getIcon(): string {
    if (this.messageService.message.type) {
      switch (this.messageService.message.type) {
        case 'ERROR':
          return './assets/error.png';
        case 'WARNING':
          return './assets/warning.png';
        default:
          return '';
      }
    } else {
      return '';
    }
  }

  public getSeverity(): string {
    if (this.messageService.message.type) {
      switch (this.messageService.message.type) {
        case 'SUCCESS':
          return 'Success: ';
        case 'ERROR':
          return 'Error: ';
        case 'WARNING':
          return 'Warning: ';

        default:
          return '';
      }
    } else {
      return '';
    }
  }

  public getWebPage(): WebPage {
    return this.messageService.webpage;
  }

  public getMessage(): string {
    if (this.messageService.message.text) {
      return this.messageService.message.text;
    } else {
      return '';
    }
  }

  public clearMessage(): void {
    this.messageService.clearMessage();
  }

  public GetMessageBorderColour(): string {
    switch (this.messageService.message.type) {
      case 'SUCCESS':
        return 'border-left-success';
      case 'WARNING':
        return 'border-left-warning';
      case 'ERROR':
        return 'border-left-error';
      default:
        return '';
    }
  }
}
