import { Injectable } from '@angular/core';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { first, skip } from 'rxjs/operators';
import { Subscription } from 'rxjs';
import { IotAnalyticsDataService } from '../IoTAnalyticsDataService/iot-analytics-data.service';
import { GitHubSearchParams } from '../../../interfaces/github-search-params';
import { WebPage } from '../../../app/message/message.component';
import { MessageService, MessageType } from '../../MessageService/message.service';
import { Constants } from '../../../classes/constants';
import { HttpErrorResponse } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class IotAnalyticsQueryParamsService {

  private subscriptions;

  public IoTAnalyticsWebpage = WebPage.IoTAnalytics;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private messageService: MessageService,
    private iotAnalyticsDataService: IotAnalyticsDataService) {
  }

  public getQueryParams(queryParams: ParamMap): GitHubSearchParams {
    return {
      text: queryParams.has('search') ? queryParams.get('search') : '',
      topic: queryParams.has('topic') ? queryParams.get('topic') : 'all-topics',
      repository: queryParams.has('repository') ? queryParams.get('repository') : 'all-repositories',
      page: queryParams.has('page') ? Number(queryParams.get('page')) : 1,
    };
  }

  initialize(): void {
    this.subscriptions = new Subscription();

    // Only update based on the query params on initial load of the page (not every time they change)
    this.route.queryParamMap
      .pipe(first())
      .subscribe(queryParams => {
      const searchParams = this.getQueryParams(queryParams);
      this.iotAnalyticsDataService.updateSearchParameters(searchParams, true);
      this.iotAnalyticsDataService.searchWithCurrentParams()
        .catch( (error) => {
          const errorResponse = (error as HttpErrorResponse);
          if (errorResponse.status === Constants.GITHUB_API_RATE_LIMIT_EXCEEDED) {
            this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_WARNING_TYPE, Constants.GITHUB_RETRIEVE_FAIL_MESSAGE);
          } else {
            this.messageService.setMessage(this.IoTAnalyticsWebpage, MessageType.CONST_ERROR_TYPE, `Github: ${errorResponse.statusText}`);
          }
      });
    });

    // Update the query string from changes to the currentSearchParams
    this.subscriptions.add(this.iotAnalyticsDataService.currentSearchParameters$
      // Skip the first item because it is the default value (from the search service initialization) - the value we actually want first is the one from the query param (above)
      .pipe(skip(1))
      .subscribe(searchParams => {
        const queryParams = {
          ...searchParams.text !== '' ? { search: searchParams.text } : undefined,
          ...searchParams.topic !== 'all-topics' ? { topic: searchParams.topic } : undefined,
          ...searchParams.repository !== 'all-repositories' ? { repository: searchParams.repository } : undefined,
          ...searchParams.page.toString() !== '1' ? { page: searchParams.page } : undefined
        };

        this.router.navigate(
          [],
          {
            relativeTo: this.route,
            queryParams,
            replaceUrl: true
          }
        );
      }));
  }

  terminate(): void {
    this.subscriptions.unsubscribe();
  }

}
