import { Component, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  selectedMenuItem = 'HOME';

  constructor(
    private router: Router) {
    this.router.events
      .subscribe(event => {
        if (event instanceof NavigationEnd) {
          switch (true) {
            case event.url.includes('connectors'):
              this.selectedMenuItem = 'CONNECTORS';
              break;
            case event.url.includes('iot'):
              this.selectedMenuItem = 'IOT';
              break;
            default:
              this.selectedMenuItem = 'HOME';
              break;
          }
        }
      });
  }

  ngOnInit(): void {
  }

  public getClassForSelected(menuItem: string): string {
    if (menuItem === this.selectedMenuItem) {
      return 'selected';
    } else {
      return 'not-selected';
    }
  }

}
