import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';


@Component({
  selector: 'app-public-custom-pagination',
  templateUrl: './custom-pagination-controls.component.html',
  styleUrls: ['./custom-pagination-controls.component.css']
})
export class CustomPaginationControlsComponent {

  constructor(private router: Router,
              private route: ActivatedRoute)  {
  }

  // tslint:disable-next-line:variable-name
  private _directionLinks = true;
  // tslint:disable-next-line:variable-name
  private _autoHide = false;
  // tslint:disable-next-line:variable-name
  private _responsive = false;

  @Input()
  get directionLinks(): boolean {
    return this._directionLinks;
  }

  set directionLinks(value: boolean) {
    this._directionLinks = value;
  }
  @Input()
  get autoHide(): boolean {
    return this._autoHide;
  }
  set autoHide(value: boolean) {
    this._autoHide = value;
  }

  @Input()
  get responsive(): boolean {
    return this._responsive;
  }
  set responsive(value: boolean) {
    this._responsive = value;
  }
  @Input() previousLabel = 'Previous';
  @Input() nextLabel = 'Next';
  @Input() screenReaderPaginationLabel = 'Pagination';
  @Input() screenReaderPageLabel = 'page';
  @Input() screenReaderCurrentLabel = `You're on page`;
  @Output() pageChange: EventEmitter<number> = new EventEmitter<number>();
  @Output() pageBoundsCorrection: EventEmitter<number> = new EventEmitter<number>();

  public getPageUrl(): string {
    return this.router.url.split('?')[0];
  }

  public getPageQueryParams(page: number): any {

    if (page !== 1) {
      // @ts-ignore
      return Object.assign({}, this.route.snapshot.queryParamMap.params, {page: page.toString()});
    } else {
      // @ts-ignore
      return Object.assign({}, this.route.snapshot.queryParamMap.params, {page: null});
    }
  }

}
